<template>
  <div class="content-main">
        <div class="header">
      <el-row>
        <el-col :span="2">
          <el-button icon="el-icon-refresh" @click="getNoticeList"></el-button>
        </el-col>
        <el-col :span="1">
          <span>id:</span>
        </el-col>
        <el-col :span="2">
          <el-input v-model="reqData.id" placeholder="请输入id"></el-input>
        </el-col>
        <el-col :offset="2" :span="2">
          <span>标题:</span>
        </el-col>
        <el-col :span="2">
          <el-input v-model="reqData.title" placeholder="请输入标题"></el-input>
        </el-col>
        <el-col :offset="9" :span="4">
          <el-button size="small" type="primary" @click.native.stop="restReqdata" plain>重置</el-button>
          <el-button size="small" type="primary" @click.native.stop="searchReqdata" plain>搜索</el-button>
          <el-button size="small" type="primary" @click.native.stop="newProject" plain>新建</el-button>
        </el-col>
      </el-row>
    </div>
    <div class="notice-table">
      <el-table :data="tableData" v-loading="loading" width="100%" size="medium">
        <el-table-column label="ID" width="100">
          <template slot-scope="scope">
            <span>{{ scope.row.id }}</span>
          </template>
        </el-table-column>
        <el-table-column label="作者" width="100">
          <template slot-scope="scope">
            <span>{{ scope.row.author }}</span>
          </template>
        </el-table-column>
        <el-table-column label="标题" width="300">
          <template slot-scope="scope">
            <span>{{ scope.row.title }}</span>
          </template>
        </el-table-column>
        <el-table-column label="发布时间" width="200">
          <template slot-scope="scope">
            <span>{{parseTime(scope.row.createtime) }}</span>
          </template>
        </el-table-column>
        <el-table-column label="修改时间" width="200">
          <template slot-scope="scope">
            <span>{{parseTime(scope.row.updatetime) }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="230" fixed="right">
          <div class="btns" slot-scope="scope">
            <el-button size="small" type="primary" @click="handleDetail(scope.$index, scope.row)">详情</el-button>
            <el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
            <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </div>
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="reqData.pageNo"
        layout="prev, pager, next"
        :total="reqData.count"
      ></el-pagination>
    </div>

    <el-dialog
      :title="newflag?'新增公告':'公告详情'"
      show-close
      top="20px"
      close-on-press-escape
      @close="closeDialog"
      :visible.sync="dialogVisible"
      width="80%"
      center
    >
      <el-form
        :disabled="disabled"
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="公告标题" prop="title">
              <el-input placeholder="请输入公告标题" v-model="ruleForm.title"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="发布作者" prop="author">
              <el-input placeholder="请输入发布作者" v-model="ruleForm.author"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="公告类型" prop="type">
              <el-select v-model="ruleForm.type" placeholder="请选择类型">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.name"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="公告封面" prop="cover">
              <el-upload
                class="avatar-uploader"
                :action="defaultUrl"
                :show-file-list="false"
                :on-success="handleCoverSuccess"
                :before-upload="beforeUploadCover"
              >
                <img v-if="ruleForm.cover" :src="ruleForm.cover" class="avatar" />
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
          </el-col>
         
        </el-row>

        <el-row>
          <el-col :span="24">
            <el-form-item label="公告详情" prop="detail">
              <vue-editor
                id="editor"
                useCustomImageHandler
                @image-added="handleImageAdded"
                v-model="ruleForm.detail"
                :disabled="disabled"
              ></vue-editor>
            </el-form-item>
          </el-col>
        </el-row>

        <el-form-item>
          <el-button v-if="newflag" type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
          <el-button v-else @click="handleUpdate" type="primary">保存</el-button>
          <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
const searchBtns = () => import("@/components/searchBtns");
// @ is an alias to /src
import { parseTime } from "../../utils";
import * as myaxios from "@/api/upload";
import * as noticeAxios from "@/api/noticemanage";
import { VueEditor } from "vue2-editor";
export default {
  name: "home",
  data() {
    return {
      socket: null,
      videoFlag: false,
      videoUploadPercent: 0,
      defaultUrl: "",
      loading: false,
      newflag: false,
      disabled: false,
      options: [
        {
          name: "未发布",
          value: 1
        },
        {
          name: "已发布",
          value: 2
        }
      ],
      reqData: {
        title: "",
        author: "",
        detail: "",
        id: "",
        title: "",
        createtime: "",
        pageNo: 1,
        pageSize: 10,
        count: 0
      },
      ruleForm: {
        title: "",
        author: "",
        detail: "",
        id: "",
        video: "",
        type: "",
        cover: ""
      },
      rules: {
        title: [
          { required: true, message: "请输入公告名称", trigger: "blur" }
          // { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" }
        ],
        author: [
          { required: true, message: "请输入公告作者", trigger: "blur" }
        ],
        type: [{ required: true, message: "请选择公告类型", trigger: "blur" }],
        cover: [{ required: true, message: "请上传公告图片", trigger: "blur" }],
        detail: [{ required: true, message: "请输入公告详情", trigger: "blur" }]
      },
      dialogVisible: false,
      content: "", //这是富文本框内容
      tableData: []
    };
  },
  created() {
    this.getNoticeList();
    this.defaultUrl = myaxios.defaultUrl;
  },
  methods: {
 
    // 上传图片封面
      // 上传图片封面
    handleCoverSuccess(res, file) {
      console.log(res);
      console.log(file);
      this.ruleForm.cover = res.data.url;
      // this.ruleForm.video = URL.createObjectURL(file.raw);
      console.log("上传成功,打印封面地址");
      console.log(this.ruleForm.cover);
    },
    // 上传图片之前
    beforeUploadCover(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
      // if (!isLt10M) {
      //   this.$message.error("上传视频大小不能超过10MB哦!");
      //   return false;
      // }
    },
    // 重置表单
    clearForm() {
      this.reqData.id = "";
      this.reqData.title = "";
      this.reqData.author = "";
      this.reqData.title = "";
      this.reqData.createtime = "";
      this.ruleForm = {
        title: "",
        author: "",
        detail: "",
        id: "",
        title: "",
        createtime: "",
        videoo: ""
      };
    },
    parseTime,
    handleUpdate() {
      this.updateNotice(this.ruleForm, this.ruleForm.id);
    },
    handleSizeChange(val) {
      this.reqData.pageSize = val;
      this.getNoticeList();
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.reqData.pageNo = val;
      this.getNoticeList();
      console.log(`当前页: ${val}`);
    },
    handleEdit(index, row) {
      this.disabled = false;
      this.dialogVisible = true;
      this.noticeDetail(row.id);
    },
    handleDelete(index, row) {
      let that = this;
      console.log("删除");
      this.ruleForm = {};
      this.$confirm("此操作将删除该公告, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          that.deleteNotice(row.id);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    // 更改公告
    async updateNotice(data, id) {
      let that = this;
      await noticeAxios.updateNotice(data, id).then(res => {
        if (res.data.success) {
          that.$message({
            type: "success",
            message: res.data.msg
          });
          that.dialogVisible = false;
          that.getNoticeList();
        } else {
          that.$message({
            type: "error",
            message: res.data.msg
          });
        }
      });
    },
    // 删除公告
    async deleteNotice(id) {
      let data = {
        id
      };
      let that = this;
      await noticeAxios.deleteNotice(data, id).then(res => {
        if (res.data.success) {
          that.$message({
            type: "success",
            message: res.data.msg
          });
          that.getNoticeList();
        } else {
          that.$message({
            type: "error",
            message: res.data.msg
          });
        }
      });
    },
    // 查询公告详情
    async noticeDetail(id) {
      let data = {
        id
      };
      let that = this;
      await noticeAxios.noticeDetail(data, id).then(res => {
        if (res.data.success) {
          that.ruleForm = res.data.data;
          that.ruleForm.createtime = that.parseTime(that.ruleForm.createtime);
          that.ruleForm.updatetime = that.parseTime(that.ruleForm.updatetime);
          console.log(that.ruleForm);
        }
      });
    },
    // 点击详情
    handleDetail(index, row) {
      this.dialogVisible = true;
      this.disabled = true;
      console.log(row);
      this.noticeDetail(row.id);
    },
    // 查询公告列表
    async getNoticeList() {
      let that = this;
      that.loading = true;
      this.reqData.createtime = parseTime(this.reqData.createtime);
      await noticeAxios
        .getNotices(this.reqData)
        .then(res => {
          if (res.data.success) {
            that.tableData = res.data.list;
            that.loading = false;
            that.reqData.count = parseInt(res.data.pageCount);
            console.log(that.reqData);
          } else {
            that.$message({
              type: "success",
              message: res.data.msg,
              duration: 1500
            });
          }
        })
        .catch(() => {
          that.loading = false;
        });
    },
    // 新增公告
    async newNotice() {
      let that = this;
      await noticeAxios.addNotice(this.ruleForm).then(res => {
        console.log(res.data);
        if (res.data.success) {
          that.dialogVisible = false;
          that.getNoticeList();
          that.$message({
            type: "success",
            message: res.data.msg,
            duration: 1500
          });
        } else {
          that.$message({
            type: "error",
            message: res.data.msg,
            duration: 1500
          });
        }
      });
    },
    // 表单重置
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.newNotice();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    handleChange(value) {
      console.log("打印子组件传递的");
      console.log(value);
    },
    newProject() {
      this.clearForm();
      this.dialogVisible = true;
      this.newflag = true;
    },
    findProject() {
      console.log(this.reqData);
      this.getNoticeList();
    },
    restProject() {
      this.clearForm();
    },
    closeDialog() {
      this.disabled = false;
      this.dialogVisible = false;
      this.newflag = false;
      this.clearForm();
      console.log("调用关闭");
    },
    // 插入图片处理
    async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      // An example of using FormData
      // NOTE: Your key could be different such as:
      // formData.append('file', file)
      let that = this;
      var formData = new FormData();
      formData.append("file", file);

      await myaxios.upload(formData).then(res => {
        const data = res.data;
        console.log(data);
        let url = res.data.url; // Get url from response
        Editor.insertEmbed(cursorLocation, "image", url);
        resetUploader();
      });
    }
  },
  components: {
    VueEditor,
    searchBtns
  }
};
</script>

<style lang="scss" scoped>
$blue: #409eff;
$white: #f5f5f5;
$gray: #d9d9d9;
.header {
  padding: 10px;
  box-sizing: border-box;
  border: 1px dashed $gray;
  border-radius: 5px;
  span {
    line-height: 40px;
    font-size: 17px;
  }
}
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.el-select {
  float: left;
}
.el-form-item__content::after {
  clear: both;
  font-size: 0;
  display: block;
  height: 0;
  content: "";
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
.upload-video {
  height: 200px;
  width: 40%;
  margin-left: 0;
}
</style>